@import "../node_modules/bootstrap/scss/bootstrap";
html {
    margin: 0;
    padding: 0;
}
// Page setup for always bottom footer
.page-container {
    position: relative;
    min-height: 100vh;
}

.content-wrapper {
    padding-bottom: 4rem;
}

.my-footer {
    position: absolute;
    bottom: 0;
    height: 4rem;
    z-index: 4;
}
// Main navbar
.navbar {
    background-color: transparent !important;
}

.navbar:before,
.navbar:after {
    content: "";
    display: block;
    height: 1px;
    left: 50%;
    margin: 0 0 0 -400px;
    position: absolute;
    width: 800px;
}

.navbar:before {
    background: #444;
    background: linear-gradient(left, #151515, #444, #151515);
    top: 58px;
}

.navbar:after {
    background: #000;
    background: linear-gradient(left, #151515, #000, #151515);
    top: 59px;
}

.own-nav {
    background: #111;
    background: linear-gradient(#1b1b1b, #111);
    border: 1px solid #000;
    border-radius: 5px;
    box-shadow: inset 0 0 0 1px #272727;
    font-size: 0px;
    margin: 15px auto 0;
    padding: 15px !important;
    z-index: 4;
    width: 512px;
}

#navbarTogglerDemo02 {
    max-width: 480px;
}

.nav-item {
    height: 35px;
}
// Nav buttons
.btn-nav {
    text-decoration: none !important;
    text-align: center;
    background: #222;
    background: linear-gradient(#333, #222);
    box-sizing: border-box;
    border: 1px solid #444;
    border-radius: 0;
    border-left-color: #000;
    box-shadow: 0 2px 0 #000;
    color: #888;
    display: block;
    float: left;
    font-family: "Cabin", helvetica, arial, sans-serif;
    font-size: 13px;
    font-weight: 400;
    height: 35px;
    line-height: 40px;
    margin-left: 0;
    padding: 0 10px;
    position: relative;
    text-shadow: 0 -1px 0 #000;
    width: 80px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.btn-nav-frst {
    border-radius: 5px 0 0 5px;
    border: 1px solid #444;
}

.btn-nav-last {
    border-radius: 0 5px 5px 0;
}

.btn-nav:hover,
.btn-nav:focus {
    background: #292929;
    background: linear-gradient(#393939, #292929);
    color: #ffc500;
    outline: none important;
    box-shadow: none;
}

.btn-nav:active {
    background: #292929;
    background: linear-gradient(#393939, #292929);
    box-shadow: 0 1px 0 #000, inset 1px 0 1px #222;
    outline: none important;
    box-shadow: none;
    top: 1px;
}
// Footer
.footer-card {
    background: #111;
    background: linear-gradient(#1b1b1b, #111);
    border: 1px solid #000;
    border-radius: 5px;
    box-shadow: inset 0 0 0 1px #272727;
    font-family: "Cabin", helvetica, arial, sans-serif;
    font-size: 13px;
    font-weight: 400;
    text-shadow: 0 -1px 0 #000;
    font-size: 13px;
    margin: auto 0 0 auto;
    padding: 10px;
    z-index: 4;
    height: 40px;
    min-width: 220px;
    width: 220px;
}

footer {
    background-color: transparent !important;
    clear: both;
    position: relative;
    margin-top: -74px;
}

footer:before,
footer:after {
    content: "";
    display: block;
    height: 1px;
    left: -1%;
    position: relative;
    margin: 0 auto 0 auto;
    z-index: -1;
}

footer:before {
    background: #444;
    background: linear-gradient(left, #151515, #444, #151515);
    top: 20px;
}

footer:after {
    background: #000;
    background: linear-gradient(left, #151515, #000, #151515);
    top: -20px;
}

.copyright {
    position: relative;
    top: -2px;
}

.page-link-shadow {
    color: #21b5ff;
    font-size: xx-large;
    text-shadow: $blue-text-shadow;
}

.top-level {
    position: relative;
    z-index: 4;
}

body {
    background-image: url("./img/background.png");
    // background: #151515;
}

// Card image
.img {
    background-size: cover;
    resize: both;
    border-radius: 5px 5px 0 0;
    height: 260px;
    background-position: top center;
    padding: 5px;
}
/* The flip card container - set the width and height to whatever you want. We have added the border property to demonstrate that the flip itself goes out of the box on hover (remove perspective if you don't want the 3D effect */
.flip-card {
    // background: #222;
    // border: 1px solid #444;
    // border-radius: 5px;
    // box-shadow: 0 2px 0 #000;
    // color: #888;
    // display: block;
    width: 200px;
    height: 300px;
    padding: 3px;
    margin: 10px 10px 10px 10px !important;
    perspective: 1000px;
    /* Remove this if you don't want the 3D effect */
}

/* This container is needed to position the front and back side */

.flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}

/* Do an horizontal flip when you move the mouse over the flip box container */

.flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
}

/* Position the front and back side */

.flip-card-front,
.flip-card-back {
    background: #222;
    border: 1px solid #444;
    border-radius: 5px;
    box-shadow: 0 2px 0 #000;
    color: #888;
    display: block;
    font-family: "Cabin", helvetica, arial, sans-serif;
    font-size: 13px;
    font-weight: 400;
    text-shadow: 0 -2px 0 #000;
    width: 200px;
    height: 300px;
    margin: 10px 10px 10px 10px !important;
    position: absolute;
    -webkit-backface-visibility: hidden;
    /* Safari */
    backface-visibility: hidden;
}

/* Style the front side (fallback if image is missing) */

.flip-card-front {
    // color: rgb(170, 170, 170);
    left: -14px;
    .card-body {
        padding: 12px 6px;
    }
}

/* Style the back side */

.flip-card-back {
    left: -14px;
    transform: rotateY(180deg);
    .card-text {
        text-shadow: 0 -1px 0 #000;
    }
    .card-body {
        padding: 0.8rem;
    }
    background: #292922;
    border-color: #ffc500;
    box-shadow: 0 0 5px rgba(255, 225, 0, 0.2), inset 0 0 5px rgba(255, 225, 0, 0.1), 0 2px 0 #000;
    color: rgb(170, 170, 170);
    outline: none;
}

